import React from "react";
// nodejs library to set properties for components
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";

import sectionStyle from "assets/jss/material-kit-react/views/landingPageSections/sectionStyle.jsx";
import "./Hobbies.scss";
import ScrollAnimation from "react-animate-on-scroll";
import Gallery from "react-grid-gallery";

function importAll(r) {
  return r.keys().map(r);
}

const imagesArray1 = importAll(
  require.context("assets/img/gallery/", false, /\.(png|jpe?g|svg)$/)
);
const imagesArray2 = importAll(
  require.context("assets/img/galleryCompressed/", false, /\.(png|jpe?g|svg)$/)
);
const IMAGES = [];
for(let i = 0; i < imagesArray1.length; i++) {
  let width = imagesArray1[i].split(".")[0].split("-")[1]
  let thumbnailWidth = imagesArray2[i].split(".")[0].split("-")[1]
  let imgObject = {
    src: imagesArray1[i],
    thumbnail: imagesArray2[i],
    thumbnailWidth: thumbnailWidth,
    // thumbnailWidth: width/4 > 100 ? width/4 : width,
    thumbnailHeight: 200,
    // tags: [
    //         { value: "Ocean", title: "Ocean" },
    //         { value: "People", title: "People" }
    //       ],
    // caption: "Boats (Jeshu John - designerspics.com)"
  };
  IMAGES.push(imgObject);
}
// imagesArray.forEach(img => {
//   let width = img.split(".")[0].split("-")[1]
//   let imgObject = {
//     src: img,
//     thumbnail: img,
//     thumbnailWidth: width/4 > 100 ? width/4 : width,
//     thumbnailHeight: 200,
//     // tags: [
//     //         { value: "Ocean", title: "Ocean" },
//     //         { value: "People", title: "People" }
//     //       ],
//     // caption: "Boats (Jeshu John - designerspics.com)"
//   };
//   IMAGES.push(imgObject);
// });

class Hobbies extends React.Component {
  render() {
    const { classes } = this.props;
    return (
      <div id="hobbies-section">
        <div className="row hobbies-container">
          <div className="col-12 hobbies-heading">
            <ScrollAnimation
              animateOnce="true"
              offset="0"
              delay="1"
              animateIn="tada"
              initiallyVisible={true}
            >
              <h1>
                <div>GALLERY</div>
              </h1>
            </ScrollAnimation>
          </div>
          <div className="col-12 hobbies-gallery">
            <Gallery 
            images={IMAGES}
            rowHeight= {300}
            enableImageSelection={false}
             />
          </div>
        </div>
      </div>
    );
  }
}

Hobbies.propTypes = {
  classes: PropTypes.object
};

export default withStyles(sectionStyle)(Hobbies);
